import api from '../api'
import JSSoup from 'jssoup'

function init () {
  var n = document.getElementById('main_form')
  // var o = document.getElementById('results')
  var e = document.getElementById('dg-url')
  var a = document.getElementById('waiting')
  var t = document.getElementById('dg-submit')

  var down = function (url) {
    api.get(url, { responseType: 'blob' }).then(() => {
      var saveLink = document.createElement('a')
      saveLink.href = url + '&dl=1'
      saveLink.className = 'button'
      saveLink.target = '_blank'
      saveLink.text = 'Download Image'
      saveLink.click()
      a.style.display = 'none'
    }, () => {
    })
  }

  var s = function (e) {
    e.style.display = 'block'
    e.classList.add('fadeIn')
    setTimeout(function () {
      e.classList.add('fadeIn-action')
    }, 10)
    setTimeout(function () {
      e.classList.remove('fadeIn-action', 'fadeIn')
    }, 410)
  }
  e.addEventListener('click', function () {
    this.select()
  })
  e.value.length > 0 &&
    setTimeout(function () {
      t.click()
    }, 1500)
  n.addEventListener('submit', function (g) {
    g.preventDefault()
    var link = e.value
    if (link === '' || link === undefined || link === null) {
      alert('Link can not be empty!')
      return
    }

    if (link.indexOf('https://www.instagram.com') < 0) {
      alert('Link is not correct!')
      return
    }
    s(a)
    // 'https://www.videopicdl.com/ins'
    //  'http://192.168.1.4:8080/ins'
    link = link.replace('https://www.instagram.com', 'https://www.videopicdl.com/ins')
    //link = 'http://localhost:3333/ins/about/'
    api.get(link).then(response => {
      //console.log("response", response)
      var soup = new JSSoup(response.body)
      var scripts = soup.findAll('script')
      var str = ''
      scripts.forEach(element => {
        if (element.text.indexOf('window._sharedData = ') >= 0) {
          str = element.text
        }
      })
      str = str.replace('window._sharedData = ', '').replace(';', '')
      var jsonS = JSON.parse(str)
      var codeMedia = jsonS.entry_data.PostPage[0].graphql.shortcode_media
      var typeName = codeMedia.__typename
      switch (typeName) {
        case 'GraphSidecar':
          var edges = codeMedia.edge_sidecar_to_children.edges
          if (edges != null && edges.length > 0) {
            edges.forEach(edge => {
              var typeName1 = edge.node.__typename
              switch (typeName1) {
                case 'GraphVideo':
                  down(edge.node.video_url)
                  break
                case 'GraphImage':
                  down(edge.node.display_resources[edge.node.display_resources.length - 1].src)
                  break
              }
            })
          }
          break
        case 'GraphVideo':
          down(codeMedia.video_url)
          break
        case 'GraphImage':
          down(codeMedia.display_resources[codeMedia.display_resources.length - 1].src)
          break
      }
    }, () => {
    })
  })
}
export {
  init
}
